.container {
  width: 85%;
  max-width: 1622px;
  margin: 0 auto;
  padding: 0 55px;
}
.index-main {
  background: url("../img/jpg/body-background.jpg") no-repeat;
  background-size: cover;
  background-attachment: fixed;
}
@font-face {
  font-family: "Gilroy-Regular";
  src: url("../fonts/Gilroy-Regular.woff"), url('../fonts/Gilroy-Regular.ttf'), url("../fonts/Gilroy-Regular.eot");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "Gilroy-Bold";
  src: url("../fonts/Gilroy-Bold.woff"), url('../fonts/Gilroy-Bold.ttf'), url("../fonts/Gilroy-Bold.eot");
  font-weight: normal;
  font-style: normal;
}
* {
  box-sizing: border-box;
}
.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
html {
  scroll-behavior: smooth;
}
a {
  outline: none;
}
ul {
  margin: 0;
}
body {
  font-family: "Gilroy-Regular", "Arial", sans-serif;
  font-size: 16px;
  font-weight: normal;
  min-width: 320px;
  padding: 0;
  margin: 0;
}
a {
  text-decoration: none;
}
.page-header {
  background-color: #063453;
}
.page-header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 80px;
}
.page-header__logo {
  height: 45px;
}
.page-header__logo img {
  width: 150px;
  height: 45px;
}
.page-header__user-wrap {
  display: flex;
}
.page-header__language-list {
  display: flex;
  align-items: center;
  list-style: none;
  text-transform: uppercase;
}
.page-header__language-item:not(:last-child) {
  margin-right: 15px;
}
.page-header__language-link {
  color: #77CCF9;
  transition: all 0.3s ease;
}
.page-header__language-link:hover,
.page-header__language-link:focus,
.active-language {
  color: #FFFFFF;
  transition: all 0.3s ease;
}
.header-nav {
  margin-left: 60px;
}
.header-nav__list {
  display: flex;
  list-style: none;
  text-transform: uppercase;
  color: #fff;
}
.header-nav__link {
  display: flex;
  align-items: center;
  position: relative;
  height: 58px;
  padding: 20px 35px 20px 65px;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.3s ease;
}
.header-nav__link::before {
  content: '';
  display: block;
  position: absolute;
  left: 30px;
  width: 20px;
  height: 20px;
  background: url("../../../img/svg/login.svg") no-repeat;
}
.login-btn::before {
  background: url("../../../img/svg/login.svg") no-repeat;
}
.registration-btn::before {
  background: url("../../../img/svg/user.svg") no-repeat;
}
.registration-btn {
  border: 2px solid #FFFFFF;
}
.greetings__container {
  min-height: 500px;
  padding-top: 195px;
  background: url('../../../img/svg/Vector.svg') no-repeat 55% 500px, url('../../../img/svg/1.svg') no-repeat 85% 700px, url('../../../img/svg/Ellipse.svg') no-repeat center -1000px;
  transition: all 1s ease;
}
.greetings__container--onload {
  background: url("../../../img/svg/Vector.svg") no-repeat 55% bottom, url("../../../img/svg/1.svg") no-repeat 85% bottom, url("../../../img/svg/Ellipse.svg") no-repeat center top;
  transition: all 1s ease;
}
.greetings__title {
  font-family: "Gilroy-Bold";
  margin: 0 auto;
  width: 535px;
  font-size: 60px;
  line-height: 78px;
  color: #063453;
}
.services {
  display: flex;
  padding-top: 80px;
  padding-bottom: 65px;
  background: #FFFFFF;
}
.services__description {
  width: 30%;
}
.services__main-title {
  margin: 0;
  font-size: 49px;
  line-height: 70px;
  color: #3D3D3D;
}
.services__description-text {
  position: relative;
  font-size: 20px;
  line-height: 30px;
  color: #464646;
  text-transform: uppercase;
  padding-bottom: 45px;
}
.services__description-text::after {
  content: '';
  display: block;
  width: 35%;
  height: 3px;
  background: #ADD9F3;
  position: absolute;
  bottom: 0;
}
.services__add-text {
  font-size: 20px;
  line-height: 30px;
  color: #7B8A99;
}
.services__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 0;
  width: 70%;
  list-style: none;
}
.services__item {
  display: block;
  width: 155px;
  height: 175px;
}
.services__link {
  display: block;
  height: 100%;
  padding-top: 130px;
  position: relative;
}
.services__link::before {
  content: '';
  display: block;
  width: 130px;
  height: 130px;
  position: absolute;
  top: 0;
  left: calc(50% - 65px);
}
.services__link--work::before {
  background: url("../../../img/svg/work.svg") no-repeat;
}
.services__link--property::before {
  background: url("../../../img/svg/property.svg") no-repeat;
}
.services__link--legalization::before {
  background: url("../../../img/svg/legalization.svg") no-repeat;
}
.services__link--ads::before {
  background: url("../../../img/svg/ads.svg") no-repeat;
}
.services__link--finance::before {
  background: url("../../../img/svg/finance.svg") no-repeat;
}
.services__link--insurance::before {
  background: url("../../../img/svg/insurance.svg") no-repeat;
}
.services__link--transport::before {
  background: url("../../../img/svg/transport.svg") no-repeat;
}
.services__link--law::before {
  background: url("../../../img/svg/law.svg") no-repeat;
}
.services__link--business::before {
  background: url("../../../img/svg/business.svg") no-repeat;
}
.services__link--blog::before {
  background: url("../../../img/svg/blog.svg") no-repeat;
}
.services__link--rest::before {
  background: url("../../../img/svg/rest.svg") no-repeat;
}
.services__link--forum::before {
  background: url("../../../img/svg/forum.svg") no-repeat;
}
.services__title {
  font-size: 16px;
  line-height: 30px;
  text-align: center;
  color: #063453;
}
.about-us {
  padding-top: 65px;
  padding-bottom: 82px;
  background: #fff;
}
.about-us__wrapper {
  padding-top: 55px;
  background: url('../../../img/png/about-us-circle-text.png') no-repeat 1045px 85%, url('../../../img/png/about-us-background.png') no-repeat 890px bottom;
}
.about-us__title {
  margin: 0;
  font-size: 49px;
  line-height: 70px;
  color: #3D3D3D;
  text-align: center;
  padding-bottom: 45px;
  position: relative;
  margin-bottom: 80px;
}
.about-us__title::after {
  content: '';
  display: block;
  height: 3px;
  width: 86px;
  position: absolute;
  bottom: 0;
  left: calc(50% - 43px);
  background-color: #ADD9F3;
}
.about-us__desc {
  max-width: 738px;
  font-size: 20px;
  line-height: 30px;
}
.about-us__bottom-text {
  max-width: 995px;
  font-size: 20px;
  line-height: 30px;
  text-align: center;
  color: #7B8A99;
}
.new-ads {
  padding-top: 82px;
  background: #FFFFFF;
}
.new-ads__title {
  font-family: "Gilroy-Regular";
  font-size: 49px;
  line-height: 70px;
  color: #3D3D3D;
  margin-bottom: 100px;
}
.new-ads__topic-list {
  padding: 0;
  display: flex;
  list-style: none;
  height: 115px;
}
.new-ads__topic-item {
  width: 33%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}
.new-ads__topic-item--active {
  background: #F2F4F5;
}
.new-ads__topic-item:nth-child(2)::before,
.new-ads__topic-item:nth-child(2)::after {
  content: '';
  display: block;
  position: absolute;
  height: 85px;
  width: 3px;
  background-color: #DCE8F1;
}
.new-ads__topic-item:nth-child(2)::before {
  left: 0;
}
.new-ads__topic-item:nth-child(2)::after {
  right: 0;
}
.new-ads__topic-circle {
  width: 80px;
  height: 80px;
  background: #ACD9F3;
  border-radius: 100px;
  position: relative;
  margin-right: 20px;
}
.new-ads__topic-circle svg {
  position: absolute;
  top: calc(50% - 21px);
  left: calc(50% - 24px);
}
.new-ads__topic-title {
  font-size: 20px;
  line-height: 30px;
  text-align: center;
  color: #063453;
}
.new-ads__work-list {
  display: flex;
  justify-content: space-between;
  list-style: none;
  padding: 0;
}
.new-ads__work-item {
  width: 355px;
  height: 435px;
  background: #FFFFFF;
}
.new-ads__item-title {
  font-size: 20px;
  line-height: 30px;
  text-transform: uppercase;
  color: #063453;
}
.new-ads__company-name {
  font-size: 16px;
  line-height: 30px;
  color: #BFCED9;
}
.new-ads__user-name {
  font-size: 18px;
  line-height: 30px;
  color: #464646;
}
.new-ads__info-wrap {
  display: flex;
  justify-content: space-between;
}
.new-ads__info-wrap--bottom {
  font-size: 16px;
  line-height: 30px;
  color: #7B8A99;
}
.new-ads__work-list {
  background: #F2F4F5;
}
.new-ads__see-all {
  display: block;
  width: max-content;
  padding: 20px 110px;
  margin-left: auto;
  font-family: "Gilroy-Bold";
  font-size: 18px;
  line-height: 30px;
  color: #7B8A99;
  background: #DCE8F1;
  border-radius: 8px;
}
